body {
  display: block;
}

select.breed-select.hidden, span.loader.hidden, span.loader.hidden:after, p.error.hidden, div.cat-info.hidden {
  visibility: hidden;
}

img {
  width: 500px;
  object-fit: contain;
  object-position: right top;
}

div.cat-info {
  background-color: #d3d3d3;
  display: flex;
}

div.text-container {
  width: 500px;
  padding-left: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-radius: 50%;
  margin: 20px;
  animation: rotation 1s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

